.notification {
  font-size: 1.25rem;
  color: green;
  background-color: lightgray;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 2px solid green;
  border-radius: 3px;
}

.errorMessage {
  font-size: 1.25rem;
  color: red;
  background-color: lightgray;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 2px solid red;
  border-radius: 3px;
}